import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AddListComponent } from './product-dispatch/add-list/add-list.component';

const routes: Routes = [
    {
        path: 'chargers',
        loadChildren: () => import('./chargers/chargers.module').then(m => m.ChargersModule),
        data: { title: 'charger' }
    },
    {
        path: 'salesDispatch',
        loadChildren: () => import('./product-dispatch/product-dispatch.module').then(m => m.ProductDispatchModule),
        data: { title: 'On Board' }
    },
    {
        path: 'dispatchedChargers',component:AddListComponent,
        data: { title: 'Dispatch Charger' }
    }
]

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class ChargerManagementRoutingModule { }