import { NgModule } from '@angular/core'; 
import { SharedModule } from 'app/shared/shared.module';
import { ChargerManagementRoutingModule } from './charger-management-routing.module';
// import { CreateComponent } from './create/create.component';
// import { ListComponent } from './list/list.component';
// import { ChargingStationRoutingModule } from './charging-station-routing.module';
//import {MatTableExporterModule} from 'mat-table-exporter';
import { FuseSharedModule  } from '@fuse/shared.module';
import {
    NgxMatDatetimePickerModule, 
    NgxMatNativeDateModule, 
    NgxMatTimepickerModule ,
    NgxMatDateFormats,
    NGX_MAT_DATE_FORMATS,
    NgxMatDateAdapter,
    
  } from '@angular-material-components/datetime-picker';
  const CUSTOM_DATE_FORMATS: NgxMatDateFormats = {
    parse: {
      dateInput: 'l, LTS'
    },
    display: {
      dateInput: 'YYYY-MM-DD HH:mm:ss',
      monthYearLabel: 'MMM YYYY',
      dateA11yLabel: 'LL',
      monthYearA11yLabel: 'MMMM YYYY',
      
    }
  };
const COMPONENTS =[];
const COMPONENTS_DYNAMIC =[];

@NgModule({
    imports:[SharedModule,FuseSharedModule, ChargerManagementRoutingModule, NgxMatDatetimePickerModule, 
        NgxMatNativeDateModule, 
        NgxMatTimepickerModule ],
    declarations : [COMPONENTS,COMPONENTS_DYNAMIC],
    entryComponents : [COMPONENTS_DYNAMIC],
    providers:[
      
        { provide: NGX_MAT_DATE_FORMATS, useValue: CUSTOM_DATE_FORMATS }
    
  ]
})

export class ChargerManagementModule{}